"use client";

import { FC, useEffect, useState } from "react";
import { ChatSession } from "@/types/ChatTypes";
import { loadChatSessions } from "@/utils/chatStorage";

interface SavedChatsSidebarProps {
  onSelect: (session: ChatSession) => void;
}

const SavedChatsSidebar: FC<SavedChatsSidebarProps> = ({ onSelect }) => {
  const [sessions, setSessions] = useState<ChatSession[]>([]);

  useEffect(() => {
    setSessions(loadChatSessions());
  }, []);

  return (
    <div className="mt-6">
      <h4 className="text-xs text-zinc-400 uppercase tracking-wide mb-2 pl-4">Chats</h4>
      <ul className="text-sm space-y-1 pl-4 pr-4">
        {sessions.map((session) => (
          <li
            key={session.id}
            onClick={() => onSelect(session)}
            className="cursor-pointer text-zinc-300 hover:text-white truncate"
            title={session.title}
          >
            {session.title}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default SavedChatsSidebar;
